.description {
  @include no-list;
}

.description--instruction {
  background-color: rgb(226, 226, 226);
  box-sizing: border-box;
  padding: 5px 10px;
}

.description__item {
  margin-bottom: 40px;
}

.description__sub {
  @include no-list;
  padding-left: 20px; 
}

.description__sub-item {
  margin-bottom: 15px;
}

.description__num {
  font-weight: bold;
  color: #353340;
}

.description__example {
  display: block;
  margin-top: 10px;
  padding: 0px 5px 0px 10px;
  border-left: 5px solid tomato;
}

@media (min-width: 768px) {
  .description--instruction {
    padding: 20px 30px;
  }
}

@media (min-width: 1120px) {
  .description__sub-item {
    margin-bottom: 25px;
  }

  .description__example {
    margin-top: 30px;
    margin-bottom: 30px;
  }
}